//import TimeProgress from '.components/TimeProgress/TimeProgress';//cut it and paste to the component
import React, {useEffect,useState} from 'react';
import './TimeProgress.scss';
import LinearProgress from '@mui/material/LinearProgress';
import Counter from "./Counter"

const TimeProgress = ({
    estimate,
    renderedTime, // this must me in seconds
    ongoing
}) => {

    const estimateInSec = estimate * 3600;

    const [progress, setProgress] = useState(renderedTime/estimateInSec*100)
 

    return <div className='TimeProgressContainer'>
        {/* <p>{timeUsed}</p> */}
        
        <p>Estimate: {estimate.toFixed(2)} hours</p>
        <div className="progress-bar-wrapper">
            {
            progress > 100 ? 
            <LinearProgress
            color="error"
            variant="determinate"
            value={100} /> : 
            <LinearProgress
            variant="determinate"
            value={progress} />
            }
        </div>
            
        {
            <Counter
            ongoing={ongoing}
            initialTimeRendered={renderedTime}
            estimateInSec={estimateInSec}
            callback={setProgress}/>
        }
        
    </div>
}

export default TimeProgress;